import image1 from '../../src/assets/images/NewsStorage/1.jpg'
import image2 from '../../src/assets/images/NewsStorage/2.jpg'
import image3 from '../../src/assets/images/NewsStorage/3.jpg'
import image4 from '../../src/assets/images/NewsStorage/4.jpg'

const images = {
    image1,
    image2,
    image3,
    image4
}

let initialState = {
    posts: [
        [
            { id: 1, text: 'Aesthetics' },
            { id: 1, text: 'Aesthetics is a philosophical teaching about the essence and forms of beauty in artistic creativity, in nature and in life, about art as a special form of social consciousness. Every person have our look on aesthetics.' },
            { id: 1, text: images.image1 }
        ],
        [
            { id: 2, text: 'Books are...' },
            { id: 2, text: 'Books are the best carrier of knowledge!' },
            { id: 2, text: images.image2 }
        ]
    ]
}

const newsReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SET_USER_DATA':
            return {
                ...state,
                ...action.payload
            }
        default:
            return state
    }
}

export default newsReducer